.KycFinancialComponent {
  height: calc(100% - 25px);
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-rows: auto auto 1fr 20%;
  grid-row-gap: 10px;
  margin-top: 25px;
}

.KycFinancialComponent p {
  color: gray;
  width: 100%;
  max-width: 750px;
  text-align: center;
}

.loaderFinancial {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  background: #ffffffc9;
  display: grid;
  align-items: center;
  justify-items: center;
}

.loaderFinancial_cont {
  position: relative;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.panelImgFinancial {
  position: relative;
  background: #f5f5f5;
  width: 100%;
  max-width: 750px;
  width: 100%;
  height: 100%;
  border-radius: 7px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr auto;
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.28);
}

.controlsFinancial {
  width: 100%;
  max-width: 750px;
  background: red;
  width: 100%;
  height: 100%;
}

.FinancialUploadImg {
  height: calc(100% - 40px);
  padding: 20px;
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
}

.KycprogressBar.financial {
  bottom: 0 !important;
  top: auto !important;
}

.FinancialUploaded {
  -webkit-transition: 0.3s;
  transition: 0.3s;
  background: -webkit-gradient(linear, left top, left bottom, from(#2786c5), to(#0062a2));
  background: linear-gradient(to bottom, #2786c5, #0062a2);
  overflow: hidden;
  display: grid;
  grid-template-rows: repeat(auto-fill, 70px);
  grid-row-gap: 10px;
}

.loaderImgComponent {
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  border: 2px dashed #3e87d9;
  border-radius: 6px;
  max-width: 450px;
  max-height: 290px;
  cursor: pointer;
  background: #f1f9ff;
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
  padding: 10px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.textFinancial {
  color: #3e87d9;
  margin: 10px 0;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-row-gap: 10px;
  align-items: center;
  text-align: center;
  justify-items: center;
  width: 100%;
  max-width: 400px;
  /*font-size: 14px;*/
}

.payloadFinancial {
  display: grid;
  justify-items: center;
  position: relative;
}

#uploadIMGF {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 1;
}

.textFinancial .iconSty {
  -webkit-animation-name: touchRUT;
          animation-name: touchRUT;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  /*animation-fill-mode: forwards;*/
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: cubic-bezier(1, 1, 1, 1);
          animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

@-webkit-keyframes touchRUT {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes touchRUT {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.item_loaded {
  width: calc(100% - 20px);
  margin: 0 10px;
  height: 100%;
  border-bottom: 1px solid white;
  align-items: center;
  color: white;
  display: grid;
  grid-template-columns: 55px 1fr 50px;
  grid-column-gap: 10px;
}

.item_loaded_text {
  color: white !important;
  font-size: 14px;
  text-align: left !important;
  margin: 0;
}

.item_loaded_text.fileName {
  font-size: 11px;
  color: #d4d4d4 !important;
  white-space: nowrap;
  width: 70px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.contIconS {
  -webkit-transform: scale(0.4);
          transform: scale(0.4);
  width: 70px;
  height: 80px;
}

